<template>
  <div>
    <div class="time" v-if="!isHappyBirthday">
      姑娘生日还有 <span>{{day}}</span> 天 <span>{{hour}}</span> 时 <span>{{min}}</span> 分 <span>{{second}}</span> 秒
    </div>
    <div v-if="isHappyBirthday">{{happybirthday}}</div>
  </div>
</template>
<script>
export default {
  name: "NanBirthday",
  data() {
    return {
      // nanBirthday: "2019/11/12 08:01:50",
      // nanBirthday: '2019/11/09 00:00:00',
      nanBirthday: '2020/11/27 00:00:00',
      happybirthday: "❤❤ 楠楠姑娘, 生日快乐!! ❤❤",
      day: null,
      hour: null,
      min: null,
      second: null,
      isHappyBirthday: false
    };
  },
  created() {
    this.setEndTime();
  },
  methods: {
    // 生日倒计时
    setEndTime() {
      let that = this;
      let interval = setInterval(function timestampToTime() {
        let date = new Date(that.nanBirthday) - new Date(); //计算剩余的毫秒数
        if (date <= 0) {
          that.isHappyBirthday = true;
          clearInterval(interval);
        } else {
          that.day = parseInt(date / 1000 / 60 / 60 / 24);
          that.hour = parseInt((date / 1000 / 60 / 60) % 24);
          if (that.hour < 10) {
            that.hour = "0" + that.hour;
          }
          that.min = parseInt((date / 1000 / 60) % 60); //计算剩余的分钟
          if (that.min < 10) {
            that.min = "0" + that.min;
          }
          that.second = parseInt((date / 1000) % 60); //计算剩余的秒数
          if (that.second < 10) {
            that.second = "0" + that.second;
          }
          return (
            that.day + "天" + that.hour + "时" + that.m + "分" + that.s + "秒"
          );
        }
      }, 1000);
    }
  }
};
</script>

<style scoped>
.time span {
  color: hotpink;
  font-size: 21px;
}
</style>
