const Nan = () => import('pages/nan')
const CarryOn = () => import('pages/carry-on')
const BeautyGirl = () => import('pages/beauty-girl')

const routes = [
  {
    path: '/',
    name: 'Nan',
    component: Nan,
  },
  {
    path: '/nx',
    name: 'BeautyGirl',
    component: BeautyGirl,
  },
  {
    path: '/carryon',
    name: 'CarryOn',
    component: CarryOn,
  },
]
export default routes;