import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueLazyload from 'vue-lazyload'

import {
  GoBack,
  NanBirthday
} from 'components'

const components = {
  GoBack,
  NanBirthday
}

Object.keys(components).forEach(key => {
  Vue.component(key, components[key])
})

Vue.use(VueLazyload)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
